
import '../App.css';
import WorldMap from 'react-world-map';
import BasicPiano from './Piano';
import ResponsivePiano from './ResponsivePiano'
import InstrumentListProvider from './InstrumentListProvider';
import InteractiveDemo from './InteractiveDemo';
import { Redirect, Route, Switch , Link, withRouter} from 'react-router-dom';
import React from 'react'
const audioContext = new (window.AudioContext || window.webkitAudioContext)();
const soundfontHostname = 'https://d1pzp51pvbm36p.cloudfront.net';

class WorldComponent extends React.Component {
  state = {
    clicked: ''
  }
  updateMyState = (continent) => {
    this.setState({ clicked: continent })
  }
  render() {
    if (this.state.clicked === "") {
      return (
       <div className="mapa">
         <div>
           <h3>click on the <span style={{'color':'red'}}>map</span> y'all</h3>
         </div>
       
        <WorldMap updateMyState={(area) => this.updateMyState(area)} />
      </div>       
      )
      


    }else if(this.state.clicked === "na"){
      return (
        <InteractiveDemo continent={this.state.clicked} audioContext={audioContext} soundfontHostname={soundfontHostname} />
      )
    }else if(this.state.clicked === "sa"){
      return (
        <InteractiveDemo continent={this.state.clicked} audioContext={audioContext} soundfontHostname={soundfontHostname} />
      )
    }else if(this.state.clicked === "af"){
      return (
        <InteractiveDemo continent={this.state.clicked} audioContext={audioContext} soundfontHostname={soundfontHostname} />
      )
    }else if(this.state.clicked === "eu"){
      return (
        <InteractiveDemo continent={this.state.clicked} audioContext={audioContext} soundfontHostname={soundfontHostname} />
      )
    }else if(this.state.clicked === "as"){
      return (
        <InteractiveDemo continent={this.state.clicked} audioContext={audioContext} soundfontHostname={soundfontHostname} />
      )
    }else if(this.state.clicked === "oc"){
      return (
        <InteractiveDemo continent={this.state.clicked} audioContext={audioContext} soundfontHostname={soundfontHostname} />
      )
    }else{
      return(
        <p>vai fazer uma conta</p>
      )
    }
  }
}


export default withRouter(WorldComponent);
