import AuthService from '../utils/auth';
import { Link, withRouter } from 'react-router-dom';
import React from 'react';
import { toast } from 'react-toastify';
import 'bootstrap/dist/css/bootstrap.css';
import 'bulma/css/bulma.css';
import '../App.css';



class Login extends React.Component {
    state = {
        username: '',
        password: ''
    }

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState({ [name]: value });
    }

    handleFormSubmit = (event) => {
        event.preventDefault();
        const { username, password } = this.state;
        const authService = new AuthService();
        authService.login(username, password)
            .then((response) => {
                this.props.setCurrentUser(response.data);
                localStorage.setItem('loggedInUser', response.data._id);

                this.props.history.push('/world');
            }).catch(() => {
               toast.error('Invalid Login')
            });

    }

    render() {
        return (
            <div className="login-page">
                <div className="container container-login column is-two-fifths pt-5">
                    <form onSubmit={this.handleFormSubmit}>
                        <div className="container field column is-half mt-5">
                            <p className="control has-icons-left has-icons-right">
                                <input className="input" type="text" name="username" value={this.state.username} onChange={this.handleChange} placeholder="Username" />
                                <span className="icon is-small is-left">
                                    <i className="fas fa-user" />
                                </span>
                                <span className="icon is-small is-right">
                                    <i placeholder="fas fa-check"></i>
                                </span>
                            </p>
                        </div>
                        <div className="container field column is-half">
                            <p className="control has-icons-left">
                                <input className="input" type="password" name="password" value={this.state.password} onChange={this.handleChange} placeholder="Password" />
                                <span className="icon is-small is-left">
                                    <i className="fas fa-lock" />
                                </span>
                            </p>
                        </div>
                        <div className="field">
                            <p className="control">
                                <button type="submit" className="button is-rounded is-info is-inverted is-outlined">
                                    Login
                        </button>
                            </p>
                        </div>
                    </form>
                </div>
            </div>



        )
    }


}

export default withRouter(Login);