import React from 'react';
import { Piano, KeyboardShortcuts, MidiNumbers } from 'react-piano';
import DimensionsProvider from './DimensionsProvider';
import InstrumentListProvider from './InstrumentListProvider';
import SoundfontProvider from './SoundfontProvider';
import PianoConfig from './PianoConfig';
import ProfileService from '../utils/api'

const noteRange = {
  first: MidiNumbers.fromNote('c3'),
  last: MidiNumbers.fromNote('f4'),
};
const keyboardShortcuts = KeyboardShortcuts.create({
  firstNote: noteRange.first,
  lastNote: noteRange.last,
  keyboardConfig: KeyboardShortcuts.HOME_ROW,
});
class InteractiveDemo extends React.Component {
  state = {
    config: {
      instrumentName: '',
      noteRange: {
        first: MidiNumbers.fromNote('c3'),
        last: MidiNumbers.fromNote('f5'),
      },
      keyboardShortcutOffset: 0,
    },
    user: {
      name: '',
      instruments: '',
    }
  };

  handleFormSubmit = () => {
    const profileService = new ProfileService()
    profileService.editProject(this.state.loggedInUser.username, this.state.loggedInUser.instruments);
    this.setState({
      name: this.state.loggedInUser.username,
      instruments: { ...this.state.loggedInUser.instruments }
    })
  }


  render() {
    const keyboardShortcuts = KeyboardShortcuts.create({
      firstNote: this.state.config.noteRange.first + this.state.config.keyboardShortcutOffset,
      lastNote: this.state.config.noteRange.last + this.state.config.keyboardShortcutOffset,
      keyboardConfig: KeyboardShortcuts.HOME_ROW,
    });
    if (this.state.config.instrumentName == '') {
      return (
        <div className="facts-page">
          <SoundfontProvider
            audioContext={this.props.audioContext}
            instrumentName={this.state.config.instrumentName}
            hostname={this.props.soundfontHostname}
            render={({ isLoading, playNote, stopNote, stopAllNotes }) => (
              <div className="container">
                <div className="row">
                  <div className="col-md-6">
                    <InstrumentListProvider
                      hostname={this.props.soundfontHostname}
                      render={(instrumentList) => (
                        <PianoConfig continent={this.props.continent}
                          config={this.state.config}
                          setConfig={(config) => {
                            this.setState({
                              config: Object.assign({}, this.state.config, config),
                            });
                            stopAllNotes();
                          }}
                          instrumentList={instrumentList || [this.state.config.instrumentName]}
                          keyboardShortcuts={keyboardShortcuts}
                        />
                      )}
                    />
                  </div>
                  <div className="col-md-6">
                    <div className="container">
                      <div width="5px">
                        <img width="400" src={`images/fact-${Math.ceil(Math.random() * 10)}.jpg`} alt="facts about refugees from the UNHCR" />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}
          />
        </div>

      );
    } else {
      return (
        <div className="piano-page">
            <div className="container">
           <div className=" d-flex">
              <p><span className="is-family-code" style={{ 'color': 'blue', 'font-size': '20px' }}>{this.state.config.instrumentName}</span></p>
              <form onSubmit={this.handleFormSubmit}>
                <button type="submit">Add this instrument to my list</button>
              </form>
            </div>
          </div>
          <DimensionsProvider>
            {({ containerWidth, containerHeight }) => (
              <SoundfontProvider
                audioContext={this.props.audioContext}
                instrumentName={this.state.config.instrumentName}
                hostname={this.props.soundfontHostname}
                render={({ isLoading, playNote, stopNote }) => (
                  <Piano
                    noteRange={noteRange}
                    width={containerWidth}
                    playNote={playNote}
                    stopNote={stopNote}
                    disabled={isLoading}
                    keyboardShortcuts={keyboardShortcuts}
                    instrumentName={this.state.config.instrumentName}
                  //{...props}
                  />

                )}
              />
            )}
          </DimensionsProvider>

        </div>
      )
    }
  }
}

export default InteractiveDemo;
