import React from 'react';
import { NavLink } from 'react-router-dom';
import AuthService from '../utils/auth';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Col, Row } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import 'bulma/css/bulma.css';


class Navbar extends React.Component {

    logoutUser = () => {
        const authService = new AuthService();
        authService.logout()
            .then(() => {
                this.props.setCurrentUser(null);
                localStorage.removeItem("loggedInUser")
            })
    }

    render() {
        if (this.props.loggedInUser) {
            return (
                <nav className="navbar is-info navbar-expand-md">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <NavLink className="nav-link has-text-warning is-family-code" exact to="/world">World</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link has-text-warning is-family-code" exact to={`/profile/${this.props.loggedInUser._id}`}>Profile</NavLink>
                        </li>
                    </ul>
                    <ul className="navbar-nav navbar-end">
                        <li className="nav-item navbar-end">
                         
                                <button className="navbar-end button has-text-warning is-outlined" onClick={this.logoutUser}>Logout</button>
                           
                        </li>
                    </ul>
                </nav>
            )
        } else {
            return (
                <nav className="navbar is-info navbar-expand-md">
                    <ul className="navbar-nav navbar-end is-transparent" >
                        <li className="nav-item is-transparent">
                            <NavLink className="nav-link has-text-danger-light is-family-code" exact to="/login">Login</NavLink>
                        </li>
                        <li className="nav-item is-transparent">
                            <NavLink className="nav-link has-text-warning is-family-code" exact to="/signup">Signup</NavLink>
                        </li>
                    </ul>
                </nav>



            )
        }
    }
}
export default Navbar;