import React from 'react';
import { MidiNumbers } from 'react-piano';

let naInstrumentList = [':)','banjo', 'electric_guitar_jazz', 'acoustic_guitar_steel', 'distortion_guitar', 'harmonica', 'gunshot'];
let saInstrumentList = [':)','marimba', 'blown_bottle', 'charang', 'tango_accordion'];
let afInstrumentList = [':)','kalimba', 'agogo'];
let euInstrumentList = [':)','glockenspiel', 'church_organ', 'french_horn', 'tuba', 'acoustic_grand_piano'];
let asInstrumentList = [':)','shakuhachi', 'sitar', 'shanai', 'shamisen', 'shakuhachi', 'taiko_drum'];
let ocInstrumentList = [':)','whistle', 'seashore', 'synth_drum'];

class AutoblurSelect extends React.Component {
  constructor(props) {
    super(props);
    this.selectRef = React.createRef();
  }

  onChange = (event) => {
    this.props.onChange(event);
    this.selectRef.current.blur();
  };

  render() {
    const { children, onChange, ...otherProps } = this.props;
    return (
      <select {...otherProps} onChange={this.onChange} ref={this.selectRef}>
        {children}
      </select>
    );
  }
}

function Label(props) {
  return <small className="mb-1 text-muted">{props.children}</small>;
}

class PianoConfig extends React.Component {
  state = {
    newInstrumentList: null,
    continentName: '',
    loaded: false
  }
  componentDidMount() {
    let continent = this.props.continent
    let newInstrumentList;
    if (continent === 'na') {
      this.setState({
        newInstrumentList: naInstrumentList,
        continentName: 'North America',
        loaded: true
      })
    } else if (continent === 'sa') {
      this.setState({
        newInstrumentList: saInstrumentList,
        continentName: 'South America',
        loaded: true
      })
    } else if (continent === 'af') {
      this.setState({
        newInstrumentList: afInstrumentList,
        continentName: 'Africa',
        loaded: true
      })
    } else if (continent === 'eu') {
      this.setState({
        newInstrumentList: euInstrumentList,
        continentName: 'Europe',
        loaded: true
      })
    } else if (continent === 'as') {
      this.setState({
        newInstrumentList: asInstrumentList,
        continentName: 'Asia',
        loaded: true
      })
    } else if (continent === 'oc') {
      this.setState({
        newInstrumentList: ocInstrumentList,
        continentName: 'Oceania',
        loaded: true
      })
    }

    window.addEventListener('keydown', this.handleKeyDown);
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyDown);
  }

  handleKeyDown = (event) => {
    const numNotes = this.props.config.noteRange.last - this.props.config.noteRange.first + 1;
    const minOffset = 0;
    const maxOffset = numNotes - this.props.keyboardShortcuts.length;
    if (event.key === 'ArrowLeft') {
      const reducedOffset = this.props.config.keyboardShortcutOffset - 1;
      if (reducedOffset >= minOffset) {
        this.props.setConfig({
          keyboardShortcutOffset: reducedOffset,
        });
      }
    } else if (event.key === 'ArrowRight') {
      const increasedOffset = this.props.config.keyboardShortcutOffset + 1;
      if (increasedOffset <= maxOffset) {
        this.props.setConfig({
          keyboardShortcutOffset: increasedOffset,
        });
      }
    }
  };

  onChangeFirstNote = (event) => {
    this.props.setConfig({
      noteRange: {
        first: parseInt(event.target.value, 10),
        last: this.props.config.noteRange.last,
      },
    });
  };

  onChangeLastNote = (event) => {
    this.props.setConfig({
      noteRange: {
        first: this.props.config.noteRange.first,
        last: parseInt(event.target.value, 10),
      },
    });
  };

  onChangeInstrument = (event) => {
    this.props.setConfig({
      instrumentName: event.target.value,
    });
  };

  render() {
    const midiNumbersToNotes = MidiNumbers.NATURAL_MIDI_NUMBERS.reduce((obj, midiNumber) => {
      obj[midiNumber] = MidiNumbers.getAttributes(midiNumber).note;
      return obj;
    }, {});
    const { noteRange, instrumentName } = this.props.config;

    return (
      <div className="form-row">
        <div className="col-md-10">
          <div className="continent-name">
            <p>{this.state.continentName}</p>
          </div>
          <div className="col is-vcentered">
            <Label></Label>
            <div className="choose-instrument">Choose an Instrument from the List</div>
            {this.state.loaded ? (<AutoblurSelect
              className="form-control"
              value={instrumentName}
              onChange={this.onChangeInstrument}>
              {this.state.newInstrumentList.map((value) => (
                <option value={value} key={value}>
                  {value}
                </option>
              ))}
            </AutoblurSelect>) : (<p>Loading</p>)}
          </div>
        </div>
      </div>
    );
  }
}

export default PianoConfig;
