import './App.sass';
import { Redirect, Route, Switch } from 'react-router-dom';
import Signup from './components/Signup';
import Login from './components/Login'
import { render } from '@testing-library/react';
import React from 'react';
import AuthService from './utils/auth';
import WorldComponent from './components/WorldComponent';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from './components/Navbar';
import Profile from './components/Profile';
import 'bootstrap/dist/css/bootstrap.css';


import Welcome from './components/WelcomePage';

class App extends React.Component {
  state = {
    loggedInUser: null
  }

  setCurrentUser = (user) => {
    this.setState({
      loggedInUser: user
    })
  }

  componentDidMount() {
    if (this.state.loggedInUser === null) {
      const authService = new AuthService();
      //check if the user session is still active on the server
      authService.loggedin()
        .then((response) => {
          if (response.data._id) {
            //theres a user session active then set the state
            //with the current user
            this.setCurrentUser(response.data);
            localStorage.setItem("loggedInUser", response.data._id)
          } else {
            localStorage.removeItem("loggedInUser");
          }
        })
    }
  }

  render() {
    return (
      <div className="App">
        <Navbar loggedInUser={this.state.loggedInUser} setCurrentUser={this.setCurrentUser} />
        <Switch>
          <Route exact path="/" component={Welcome} />
          <Route exact path="/world" component={WorldComponent} />
          <Route exact path="/world" render={
            () => {
              if (localStorage.getItem("loggedInUser")) {
                return <Redirect to="/world" />
              } else {
                return <Redirect to="/login" />
              }
            }
          } />
          <Route path="/signup" component={Signup} />
          <Route path="/login" render={
            () => {
              return <Login setCurrentUser={this.setCurrentUser} />
            }
          } />
          <Route path="/login-google" render={
            () => {
              window.location.href = `${process.env.REACT_APP_PROJECTS_API}/api/auth/google`
            }
          } />
          <Route path='/profile/:id' render={
            () => {
              return <Profile loggedInUser={this.state.loggedInUser} setCurrentUser={this.setCurrentUser} instruments={this.state.instruments}/>
            }
          } />
        </Switch>
      </div>
    );
  }
}

export default App;
